
body {
  font-family:"Arial Narrow";
}

img {
  height: 50px;
  width: 50px;
}
#navigationBar {
  background:#444;
  -webkit-box-shadow:0px 1px 10px 10px #ddd;
          box-shadow:0px 1px 10px 10px #ddd;
  height:auto;
  text-align:center;
  width:100%;
}
#navigationBar > ul > li {
  display:inline;
  padding:100px%;
}
#navigationBar > ul > li > a
{
  color:#fff;
  display:inline-block;
  height:60px;
  line-height:60px;
  margin:0px;
  padding:0px 15px;
  text-decoration:none;
  -webkit-transition: color 0.3s ease-in;
  -o-transition: color 0.3s ease-in;
  transition: color 0.3s ease-in;
}
#navigationBar > ul > li > a:hover {
  color:#FCD000;
}



